header{
    height: 100vh;
    padding-top: 5rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height:100%;
    position:relative;

}

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
    margin-bottom: 2.5rem;
}

.me{
    display: flex;
    justify-content: center;
    overflow:hidden;
    
    padding: 5rem 1.5rem 1.5rem 1.5rem;

    
   
}
.me img{
    border-radius: 5px;
}

/*============Socials=========*/
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:1.8rem;
    position: absolute;
}

.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

@media screen and (max-width: 1024px){
    header{
        height: 68vh;
    }

}

@media screen and (max-width: 600px){
    header{
        height: 1000vh;
    }
    .header__socials,
    .scroll_down{
        display: none;
    }

}